


select {
    color:#2d9dae;
    outline:none;
    padding:0px;
    border:1px solid rgb(0, 0, 0,0.25);
    font-weight:500px;
    letter-spacing: 1px;
    font-size:20px;
    background-color:white;
}

.select-selected:after {
    position: absolute;
    content: "";
    top: 14px;
    right: 10px;
    width: 0;
    height: 0;
    border: 6px solid transparent;
    border-color: #fff transparent transparent transparent;
  }

.tshirt{
    display:-ms-flexbox;
    display:flex;
    font-size:20px;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align:center;
        align-items:center;
    -ms-flex-direction:row;
        flex-direction:row;
}

@media(max-width:640px){
    .tshirt{
        font-size:14px;
    }
}




