.maina{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    padding:30px 0px;
}

.appstore_logos{
    height:60px;
    width:400px;
    background-position: center;
    background-size: cover;
    background-image: url('../../Assets/GAS.png')
}

@media(max-width:640px){
    p{
        font-size:12px;
    }
    
    .appstore_logos{
        height:60px;
        width:100px;
        background-position: center;
        background-size: cover;
        background-image: url('../../Assets/GAS.png')
    }
}











