
@import url('https://fonts.googleapis.com/css?family=Open+Sans');
@import url('https://fonts.googleapis.com/css?family=Varela+Round');

html, body {
  display:-ms-flexbox;
  display:flex;
  -ms-flex-direction: column;
      flex-direction: column;
  margin: 0;
  padding: 0;
  height:100%;
  font-size:20px;
  line-height:1.3;
  font-family: 'Open Sans', 'Helvetica Neue', 
  sans-serif;
}

#root {
  display:-ms-flexbox;
  display:flex;
  -ms-flex:1 1;
      flex:1 1;
  -ms-flex-direction: column;
      flex-direction: column;
  margin:0;
  padding:0;
}

a {
  color: rgba(67,67,72);
  cursor:pointer;
  padding:10px 20px;
  text-align:'center';
  font-weight: 700;
  border-radius:20px;
  outline:none;
  font-size:10px;
  background: none;
  border:none;
  text-decoration:underline;
  -webkit-transition:1s cubic-bezier(0.02, 0.8, 0.2, 1);
  -o-transition:1s cubic-bezier(0.02, 0.8, 0.2, 1);
  transition:1s cubic-bezier(0.02, 0.8, 0.2, 1);
}

a:hover{
  color:rgb(67,67,72);
  background-color:rgba(255,255,255);
  -webkit-box-shadow: 0 10px 20px rgba(0,0,0,0.25);
          box-shadow: 0 10px 20px rgba(0,0,0,0.25);
  -webkit-transform: translateY(-3px);
      -ms-transform: translateY(-3px);
          transform: translateY(-3px)
}

.main{
  color:rgb(67,67,72);
  display:grid;
  font-size:20px;
  justify-items:center;
  -ms-flex-align:center;
      align-items:center;
  grid-auto-rows: repeat(5,auto);
}

input {
  outline:none;
  caret-color: #2d9dae;
  font-size:16px;
  font-weight:500;
  letter-spacing: 1px;
  width:400px;
  height:30px;
  padding:5px 10px;
  border:none;
  border-bottom: 2px solid rgba(0,0,0, 0.25);
  -webkit-transition: 0.5s ease-in;
  -o-transition: 0.5s ease-in;
  transition: 0.5s ease-in;
}

input:focus{
  border-bottom: 2px solid #2d9dae;
}

.accept_btn{
  outline:none;
  color:white;
  width:150px;
  font-size:20px;
  padding:10px;
  cursor: pointer;
  border-radius:20px;
  background-image: url('./Assets/btnBg1.png');
  text-transform: uppercase;
  letter-spacing: 1.2px;
  -webkit-box-shadow:0px 10px 20px rgba(0,0,0, 0.25);
          box-shadow:0px 10px 20px rgba(0,0,0, 0.25);
  border: 1px solid rgb(255,255,255,0.75);
  -webkit-transition: 0.5s ease-in;
  -o-transition: 0.5s ease-in;
  transition: 0.5s ease-in;
}

.accept_btn:hover{
  outline:none;
  color:white;
  font-size:20px;
  font-weight: 500px;
  padding:10px;
  cursor: pointer;
  border-radius:20px;
  background-color: #2B5B37;
  text-transform: uppercase;
  letter-spacing: 1.2px;
  -webkit-box-shadow:0px 10px 20px rgba(0,0,0, 0.25);
          box-shadow:0px 10px 20px rgba(0,0,0, 0.25);
  border: 1px solid rgb(255,255,255,0.75);
  -webkit-transform: translateY(-3px);
      -ms-transform: translateY(-3px);
          transform: translateY(-3px)
}

.center {
  -ms-flex: 1 1;
      flex: 1 1;
  height: 100%;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  border-radius: 3px;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack:center;
      justify-content:center;
  -ms-flex-direction: column;
      flex-direction: column;
}

.title {
  margin:5px 0px;
  color: #2d9dae;
  font-size: 100px;
  margin: 0;
  letter-spacing:1px;
  font-family: 'Varela Round', sans-serif;
}

.title2 {
  margin:45px 0px;
  color: #2d9dae;
  font-size: 72px;
  margin: 0;
  letter-spacing:1px;
  font-family: 'Varela Round', sans-serif;
}

textarea {
  margin:20px 0px;
  outline:none;
  font-weight:500;
  letter-spacing: 1px;
  font-size:16px;
  width:400px;
  height:100px;
  padding:5px 10px;
  border:none;
  border-bottom: 2px solid rgba(0,0,0, 0.25);
  -webkit-transition: 0.5s ease-in;
  -o-transition: 0.5s ease-in;
  transition: 0.5s ease-in;
}

textarea:focus{
  border-bottom: 2px solid #2d9dae;
}


@media(max-width:640px){

  .title {
      font-size: 32px;
  }

  .title2 {
    font-size: 24px;
  }

  input {
    width:200px;
  }

  textarea{
    width:200px;
  }

  .accept_btn{
    font-size:14px;
    padding: 5px;
  }

}









